@import "../../styles/colors.scss";

.tour-item {
  background-color: #fff;
  border: 1px solid rgba(0,0,0,.09);
  box-shadow: 0 1px 7px rgba(0,0,0,.05);
  border-radius: 4px;
  max-width: 320px;
  min-height: 400px;
  text-align: center;
  &:hover{
    border: 1px solid rgba(0,0,0,.15);
    box-shadow: 0 1px 7px rgba(0,0,0,.1);
  }

  margin: 15px 0;
  .text-tour{
    padding: 15px;
    .tour-name {
      display: block;
      font-size: 16px;
      font-weight: 700;
    }
    .time-tour-item{
      color:#666;
      font-size: 14px;
      font-weight: 400;
    }
    .des {
      margin-top: 10px;
      text-align: justify;
      overflow: hidden;
      max-height: 100px;
      margin-bottom: 10px;
      p{
        font-size: 14px;
        line-height: 20px;
        margin-bottom: 0px !important;
      }
    }
  }
}

.fl-team-member-item {
  min-height: 180px;
  overflow: hidden;
  color: #fff;
}

